import React from 'react';
import styled from "styled-components";
import { inject, observer } from "mobx-react";
import { Redirect, withRouter } from "react-router";
import { routes, cabinetStates } from "consts/routes";
import { TabView, TabPanel } from "primereact/tabview";
import LikedApartments from "components/–°abinet/LikedApartments";
import VisitedApartments from "components/–°abinet/VisitedApartments";
import MySubscriptions from "components/–°abinet/MySurscriptions";

const data = Array.from(Object.values(cabinetStates));

const Panes = {
    // [cabinetStates.myApartments]: {component: <MyApartments />, title: '–ú–æ—ó –æ–≥–æ–ª–æ—à–µ–Ω–Ω—è'},
    [cabinetStates.visited]: {component: <VisitedApartments />, title: '–ü–µ—Ä–µ–≥–ª—è–Ω—É—Ç—ñ –º–Ω–æ—é'},
    [cabinetStates.subscriptions]: {component: <MySubscriptions />, title: '–ú–æ—ó –ø—ñ–¥–ø–∏—Å–∫–∏'},
    [cabinetStates.liked]: {component: <LikedApartments />, title: '–ó–±–µ—Ä–µ–∂–µ–Ω—ñ'},
    // [cabinetStates.messages]: {component: <div style={{height: 400}} />, title: '–ü–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è'}
}



const Cabinet = ({ userStore, location, history }) => {
    if (userStore.isLoading) return <div className={'loading'}/>
    if (userStore.isLogged) {
        const activeIndex = data.indexOf(location.pathname.substr(8))
        return (
            <Container>
                <TabView
                    renderActiveOnly
                    activeIndex={activeIndex}
                    onTabChange={e => history.push(routes.CABINET + data[e.index])}
                >
                    {data.map(page =>  {
                        if (Panes[page]) return <TabPanel key={page} header={Panes[page].title}>
                            {Panes[page].component}
                        </TabPanel>
                        return null;
                    }).filter(el => el)}
                </TabView>
            </Container>
        )
    }
    return <Redirect to={routes.BASE} />
}

export default inject('userStore')(withRouter(observer(Cabinet)));

const Container = styled.div`
  margin: auto;
  max-width: ${props => props.theme.consts.containerWidth};
`;
