import React, { useState, useEffect } from 'react';
import styled from "styled-components";
import { inject, observer } from "mobx-react";
import { getSubscription } from "api/subscriptions";
import { PrimaryButton } from "components/public/buttons";
import { routes } from "consts/routes";
import { Link } from "react-router-dom";

const MySubscriptions = ({ userStore }) => {
    const [ subscription, setSubscription ] = useState(null);

    useEffect(() => {
        if (userStore.user.subscription && userStore.user.hasSubscription) getSubscription(userStore.user.subscription)
            .then(data => {
                setSubscription(data);
            })
    }, [ userStore.user.hasSubscription, userStore.user.subscription ]);

    if (subscription) return (
        <Container>
            <h2>–í–∞—à –≤–∞—Ä—ñ–∞–Ω—Ç –ø—ñ–¥–ø–∏—Å–∫–∏: {subscription.name}</h2>
            <h3>–í–∞—Ä—Ç—ñ—Å—Ç—å: {subscription.price} ‚Ç¥</h3>
            <h4>–£–º–æ–≤–∏:</h4>
            <ul>{subscription.description?.trim().split('     ').map(el => <li>{el}</li>)}</ul>
        </Container>
    )

    return <NoSubscriptionsContainer>
        <h2>–£ –í–∞—Å –Ω–µ–º–∞—î –∂–æ–¥–Ω–æ—ó –∞–∫—Ç–∏–≤–Ω–æ—ó –ø—ñ–¥–ø–∏—Å–∫–∏</h2>
        <p>–ü—ñ–¥–ø–∏—Å–∫–∏ –ø–æ—Ç—Ä—ñ–±–Ω—ñ –¥–ª—è —Ç–æ–≥–æ, —â–æ–± –¥–∏–≤–∏—Ç–∏—Å—è –∫–æ–Ω—Ç–∞–∫—Ç–∏ –≤–ª–∞—Å–Ω–∏–∫—ñ–≤ –∫–≤–∞—Ä—Ç–∏—Ä, —è–∫—ñ –º–∏ –≤—ñ–¥–±–∏—Ä–∞—î–º–æ –≤—Ä—É—á–Ω—É</p>
        <Link to={routes.TARRIFS}>
            <PrimaryButton>–ü–µ—Ä–µ–≥–ª—è–Ω—É—Ç–∏ –¥–æ—Å—Ç—É–ø–Ω—ñ –≤–∞—Ä—ñ–∞–Ω—Ç–∏ –ø—ñ–¥–ø–∏—Å–æ–∫</PrimaryButton>
        </Link>
    </NoSubscriptionsContainer>

}




export default inject('userStore')(observer(MySubscriptions));

const Container = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 900px;
  width: 100%;
  margin: auto;
  
  h4 {
    margin-bottom: 0;
  }
`;

const NoSubscriptionsContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  height: 300px;
  a {
    text-decoration: none;
    width: max-content;
    align-self: center;
  }
  button {
    width: max-content;
  }
`;